





































































































import axios from 'axios'
import {Component, Vue} from 'vue-property-decorator'
import {Menu} from '@/models/Menu'
import Modal from '@/components/Modal.vue'
import configService from '@/services/config.service'

@Component({
  components: {
    Modal
  }
})
export default class AdminMenu extends Vue {
  modal: boolean = false
  help: boolean = false
  title: string = '添加菜单'
  menu: Menu = new Menu()
  menus: Menu[] = []

  mounted() {
    configService.setTitle('菜单配置')
    this.load()
  }

  load() {
    axios.get('/admin/menus').then(({data}) => {
      this.menus = data.content
    })
  }

  editMenu(menu: Menu) {
    Object.assign(this.menu, menu)
    this.title = '编辑菜单'
    this.modal = true
  }

  deleteMenu(menu: Menu) {
    axios.delete('/admin/menus/' + menu.id).then(() => {
      this.$store.dispatch('getMenus')
      this.$toasted.success('删除菜单成功')
      this.load()
    })
  }

  addMenu() {
    this.title = '添加菜单'
    this.modal = true
  }

  onCancel() {
    if (this.menu.id) {
      this.menu = new Menu()
    }
    this.modal = false
  }

  onSubmit() {
    if (!this.menu.title) {
      return
    }
    let api = '/admin/menus'
    if (this.menu.id) {
      api = '/admin/menus/' + this.menu.id
    }
    axios.post(api, this.menu).then(() => {
      this.menu = new Menu()
      this.modal = false
      this.$store.dispatch('getMenus')
      if (this.menu.id) {
        this.$toasted.success('更新菜单成功')
      } else {
        this.$toasted.success('添加菜单成功')
      }
      this.load()
    })
  }
}
